@import "./colors";
@import "./fonts";
@import "./mixins";
.main {
  .content {
    @include flexMixin(row nowrap, normal, normal);
    background-color: $defaultColor;
    color: black;
    padding: 3% 4%;
    .short-info {
      .poster {
        display: block;
        margin-top: -40%;
      }
      .button {
        @include textMixin(
          $robotoFont,
          $defaultColor,
          13.6px,
          normal,
          normal,
          16px,
          none,
          0
        );
        padding: 10px 44px;
        background-color: $orangeText;
        margin-top: 20px;
        margin-bottom: 30px;
        border: none;
      }
      .actors {
        @include textMixin(
          $robotoFont,
          black,
          16px,
          normal,
          900,
          150%,
          none,
          0
        );
        margin-bottom: 10px;
      }
      .actors-list {
        .actors-list-item {
          a {
            @include textMixin(
              $robotoFont,
              black,
              16px,
              normal,
              normal,
              150%,
              none,
              0
            );
            text-decoration: underline;
            &:hover {
              color: $orangeText;
              font-size: 17px;
            }
          }
        }
      }
    }
    .full-info {
      margin-left: 4%;
      width: 79%;
      display: inline-block;
      .tabs {
        margin-bottom: 4%;
        ul {
          @include flexMixin(row wrap, space-between, center);
          .tabs-item {
            .active {
              color: $orangeText;
            }
            a {
              @include textMixin(
                $robotoFont,
                black,
                18px,
                normal,
                normal,
                21px,
                none,
                0
              );
              text-decoration: none;
              @media (max-width: 1200px) {
                font-size: 16px;
              }
            }
            &:last-child {
              cursor: pointer;
              transition: all 0.3s linear;
            }
          }
        }
      }
      .main-block {
        @include flexMixin(row nowrap, normal, normal);
        .main-info {
          width: 70%;
          margin-right: 70px;
          @include textMixin(
            $robotoFont,
            black,
            16px,
            normal,
            normal,
            140%,
            none,
            0
          );
          h3 {
            @include textMixin(
              $robotoFont,
              black,
              16px,
              normal,
              900,
              140%,
              none,
              0
            );
          }
          @media (max-width: 1200px) {
            width: 60%;
            margin-right: 60px;
          }
          @media (max-width: 992px) {
            width: 56%;
            margin-right: 50px;
            font-size: 13px;
          }
        }
        .sidebar {
          width: 30%;
          @include flexMixin(column nowrap, normal, center);
          @media (max-width: 1200px) {
            width: 40%;
          }
          @media (max-width: 992px) {
            width: 46%;
          }
          h3 {
            @include textMixin(
              $robotoFont,
              black,
              16px,
              normal,
              900,
              150%,
              none,
              0
            );
            margin-bottom: 14px;
            @media (max-width: 992px) {
              font-size: 14px;
            }
          }
          .sybscribe-form {
            @include flexMixin(row nowrap, normal, normal);
            margin-bottom: 48px;
            @media (max-width: 400px) {
              transform: scale(0.9);
            }
            .subscribe-field {
              padding: 12px 0 12px 5px;
              border: none;
              border-top-left-radius: 5px;
              border-bottom-left-radius: 5px;
              color: #9b9b9b;
              background: rgba(241, 241, 241, 0.5);
              &::placeholder {
                @include textMixin(
                  $robotoFont,
                  #9b9b9b,
                  14px,
                  normal,
                  normal,
                  16px,
                  none,
                  0
                );
              }
            }
            .check {
              background-color: $orangeText;
              border-top-right-radius: 5px;
              border-bottom-right-radius: 5px;
              border: none;
              padding: 12px;
            }
          }
        }
      }
    }
  }
}

@media (max-width: 765px) {
  .main {
    .content {
      flex-direction: column;
      .short-info {
        display: flex;
        flex-direction: column;
        align-items: center;
        .poster {
          margin-top: -65px;
        }
        .actors {
          display: none;
        }
        .actors-list {
          display: none;
        }
        .button {
          padding-left: 75px;
          padding-right: 75px;
        }
      }
      .full-info {
        width: 100%;
        margin-left: 0;
        .tabs {
          ul {
            flex-direction: column;
            align-items: flex-start;
            margin-bottom: 35px;
            li {
              &:last-child {
                display: none;
              }
            }
          }
        }
        .main-block {
          flex-direction: column;
          .main-info {
            width: 100%;
            margin-right: 0;
            margin-bottom: 4%;
          }
          .sidebar {
            width: 100%;
            .sybscribe-form {
              transform: scale(1);
            }
          }
        }
      }
    }
  }
}
