@import "./colors";
@import "./fonts";

@mixin flexMixin($flow,$justify,$align) {
  display: flex;
  flex-flow: $flow;
  justify-content: $justify;
  align-items: $align;
}

@mixin textMixin(
  $family,
  $color,
  $size,
  $style,
  $weight,
  $height,
  $transform,
  $spacing
) {
  font-family: $family;
  color: $color;
  font-size: $size;
  font-style: $style;
  font-weight: $weight;
  line-height: $height;
  text-transform: $transform;
  letter-spacing: $spacing;
}
