@import './colors';
@import './fonts';
@import './mixins';

.actors-cards{
    @include flexMixin(row wrap,normal,center);
    @media screen and (max-width:768px){
        justify-content: center;
    }
    .actor-card{
        max-width: 155px;
        height: 230px;
        margin-right: 12px;
        margin-bottom: 20px;
        img{
            width: 100%;
            height: 90%;
            margin-bottom: 10px;
            object-fit: cover;
            border-radius: 5px;
        }
        h5{
            @include textMixin($robotoFont,$mainBgColor,18px,normal,normal,18px,none,0);
            text-align: center;
        }
        &:hover{
            cursor: pointer;
            box-shadow: grey;
            img{
                transform: scale(1.01);
            }
            h5{
                text-decoration: underline;
                color: $orangeText;
            }
        }
    } 
}
