@import './colors';
@import './fonts';
@import './mixins';

.feedback-card{
    border-radius: 8px;
    padding: 10px 20px;
    background-color: #e9e9e9;
    margin-bottom: 15px;
    .user-info{
        @include flexMixin(row nowrap,space-between,center);
        margin-bottom: 15px;
    }
    &:last-child{
        margin-bottom: 0;
    }
}