@import './fonts';
@import './mixins';

.empty-info{
    @include flexMixin(column nowrap,center,center);
    h2{
        @include textMixin($robotoFont,#CBCBCB,25px,normal,normal,23px,none,0);
        text-align: center;
    }
    img{
        transform: scale(0.3);
        opacity: 0.2;
    }
}