@import "./colors";
@import "./fonts";
@import "./mixins";
// стили для шапки
.header {
  @include flexMixin(row wrap, space-between, center);
  padding-top: 45px;
  margin-bottom: 30%;
  .logo {
    @include textMixin(
      $robotoFont,
      $defaultColor,
      24px,
      normal,
      900,
      28px,
      none,
      0
    );
  }
  .search {
    @include flexMixin(row nowrap, normal, center);
    flex: 0.8;
    .search-input {
      flex: 1;
      padding: 12px 0px 12px 18px;
      background: rgba(196, 196, 196, 0.5);
      border-radius: 5px;
      border: none;
      color: #aeaeae;
      font-size: 14px;
      line-height: 16px;
      &::placeholder {
        color: #aeaeae;
      }
    }
    .search-button {
      padding: 10px 18px;
      background-color: transparent;
      border: none;
    }
  }
  .user {
    @include flexMixin(row nowrap, normal, center);
    .avatar {
      margin-left: 20px;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      object-fit: cover;
    }
  }
}

@media (max-width: 765px) {
  .header {
    justify-content: space-between;
    padding: 0 20px;
    padding-top: 25px;
    .search {
      order: 3;
      flex: 1;
      margin-top: 4%;
    }
  }
}
.movie-block-list {
  position: absolute;
  width: 37.3%;
  left: 31.6%;
  top: 10%;
  @media screen and (max-width: 1024px) {
    width: 55.3%;
    left: 22.7%;
    top: 7%;
  }
  @media screen and (max-width: 800px) {
    width: 51.3%;
    left: 24.7%;
    top: 9%;
  }
  @media screen and (max-width: 768px) {
    width: 278px;
    left: 20px;
    top: 125px;
  }
  @media screen and (max-width: 415px) {
    width: 315px;
  }
  @media screen and (max-width: 380px) {
    width: 279px;
  }
  @media screen and (max-width: 360px) {
    width: 265px;
  }
  @media screen and (max-width: 325px) {
    width: 225px;
  }
  .movie-list {
    background: rgba(196, 196, 196, 0.5);
    border-radius: 5px;
    li {
      @include textMixin($robotoFont,$defaultColor,15px,normal,normal,15px,none,0);
      cursor: pointer;
      padding-left: 10px;
      margin-bottom: 10px;
      &:first-child {
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;
        padding-top: 5px;
      }
      &:last-child {
        padding-bottom: 5px;
        border-bottom-left-radius: 5px;
        border-bottom-right-radius: 5px;
      }
      &:hover {
        background-color: rgb(223, 223, 223);
      }
    }
  }
}
