@import './reset';
@import './animate';
@import "./colors";
@import './header';
@import './intro-section';
@import './content-section';
@import './feedbackcard';
@import './actor-card';
@import './not-found-block';
html{
    overflow-x: hidden;
    box-sizing: border-box;
}
.container{
    max-width: 1140px;
    margin: 0 auto;
}
img{
    user-select: none;
    pointer-events: none;
}
body{
    background-color: $mainBgColor;
    background-image: url(../img/main-bg.png);
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: top center;
}
@media (max-width:1200px){
    .container{
        max-width: 960px;
    }
}
@media (max-width:992px){
    .container{
        max-width: 750px;
    }
}
@media (max-width:765px){
body{
    background-size: initial;
}
}