@import './colors';
@import './fonts';
@import './mixins';
.main{
    .title{
        @include textMixin($playfairFont,$defaultColor,48px,normal,bold,64px,none,0);
    margin-bottom: 7px;
    }
    .betman-en{
        display: inline-block;
        @include textMixin($robotoFont,#787878,20px,normal,normal,23px,none,0);
        margin-bottom: 38px;
    }
    .info{
        @include flexMixin(row nowrap,space-between,center);
        margin-bottom: 100px;
        .movie-info{
            @include flexMixin(row nowrap,space-between,center);
           .type{
               @include textMixin($robotoFont,black,16px,normal,normal,19px,none,0);
               padding: 5px 10px;
               background-color: $defaultColor;
               border-radius: 3px;
               margin-right: 20px;
           }
           .country{
               @include textMixin($robotoFont,$defaultColor,16px,normal,normal,19px,none,0);
            margin-right: 33px;
            }
            a{
                @include textMixin($robotoFont,$defaultColor,16px,normal,normal,19px,none,0);
                @include flexMixin(row nowrap,normal,center);
                text-decoration: none;
                &::before{
                    content: '';
                    width: 30px;
                    height: 30px;
                    background-image: url(../img/play.svg);
                    background-repeat: no-repeat;
                    background-size: 100%;
                    margin-right: 15px;
                }
            }
        }
        .more{
            @include textMixin($robotoFont,$defaultColor,16px,normal,normal,19px,none,0);
            @include flexMixin(row nowrap,normal,center);
            text-decoration: none; 
            &::after{
                content: '';
                transform: rotate(90deg);
                width: 30px;
                height: 30px;
                background-image: url(../img/play.svg);
                background-repeat: no-repeat;
                background-size: 100%;
                margin-left: 15px;
            }
        }
    }
   
}

@media (max-width:765px){
    .main{
        padding: 0 20px;
        margin-top: 168px;
        .info{
            .type,.country{
                margin-bottom: 25px;
            }
            .movie-info{
                flex-wrap: wrap;
            }
          .more{
            display: none;
          }
        }
      }
}